$primary-color-grey: #989a9f;

.header-menu {
    display: flex;
    justify-content: space-evenly;

    .item-link {
        color: #989a9f;
        font-weight: 400;
        font-size: 13px;
        display: flex;
        position: relative;

        &.active {
            color: #2b78e4;

            svg {
                fill: #2b78e4;
            }
        }
    }

    .direct-icon, .inbox-icon, .build-icon, .people-icon {
        height: 16px;
        margin-right: 8px;
        width: 16px;
    }

    .circle-item {
        background: #ff9900;
        border-radius: 50%;
        height: 6px;
        left: 14px;
        position: absolute;
        width: 6px;
    }
}

.chat-wrapper {
    display: flex;
    flex: 1;

    *, ::after, ::before {
        box-sizing: border-box;
    }

    .icon-button {
        display: flex;

        &:hover {
            background: none !important;
        }
    }

    .divider {
        text-align: center;
        margin: 15px;
        position: relative;

        &:before {
            border-top: 1px solid #eeecec;
            content: '';
            position: absolute;
            width: 100%;
            left: 0;
            top: 12px;
        }

        span {
            font-size: 11px;
            color: #989a9f;
            display: inline-block;
            position: relative;
            z-index: 1;
            background: #f9fdff;
            padding: 0 30px;
        }

        &.new-messages {
            &:before {
                border-top-color: red;
            }

            span {
                color: red;
            }
        }
    }

    .arrow-icon {
        transition: .2s;
        transform: rotate(90deg);
        height: 16px;
        width: 16px;
    }

    .cp-Panel-toggle {
        color: #121416;
        display: flex;
        justify-content: space-between;
        font-weight: 500;
        padding: 10px 0;

        &.team-members,
        &.pinned-items {
            padding-bottom: 10px;
        }

        &.cp-is-open {
            .arrow-icon {
                transform: rotate(0deg);
            }

            .svg-icon {
                fill: #2b78e4;
            }

            .menu-link {
                font-weight: 700;
                color: #2b78e4;
            }
        }

        > .unread .menu-link {
            > span {
                font-weight: 700;
                color: #2b78e4;
            }

            > svg {
                fill: #2b78e4;
            }
        }
    }

    .actions-input {
        background-color: #fff;
        border-radius: 0 0 5px 5px;
        border: 1px solid #eeecec;
        border-top: none;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 5px;

        &.focus {
            border-color: #b1b1b1;
        }

        > div > span {
            cursor: pointer;
            display: inline-block;
            margin: 0 5px;

            &.active, &:hover {
                .svg-icon {
                    fill: #2b78e4;
                }
            }
        }

        .svg-icon {
            height: 18px;
            width: 18px;
        }
    }

    .chat-input {
        padding: 10px;
        position: relative;

        .form-send-button {
            background-color: #2B78E4;
            border-radius: 4px;
            padding: 4px 4px 0;

            .svg-icon {
                fill: #fff;
            }

            &:hover {
                background-color: #1E20A8;

                .svg-icon {
                    fill: #fff;
                }
            }

            &[disabled] {
                background-color: #fff;
                pointer-events: none;

                .svg-icon {
                    fill: #989a9f;
                }
            }
        }

        .application-loading-wrapper {
            margin-top: 0;

            .loader > div {
                height: 12px;
                width: 12px;
            }
        }

        .wrapper-icon-chat {
            left: 14px;
            bottom: 100px;
            right: auto;
            top: auto;
        }

        .popup-open {
            z-index: 3;

            .apps-popup {
                display: block;
            }
        }

        .action-field {
            display: flex;
            margin: 5px 0;
        }

        .kb-search-result {
            max-height: 235px;
            overflow: auto;

            .loadingText {
                padding: 10px;
            }

            .search-item {
                background-color: #fff;
                box-shadow: 0 0 2px 1px rgb(0 0 0 / 8%);
                border: solid 1px #9e9e9e;
                border-radius: 4px;
                margin-bottom: 5px;
                padding: 2px 10px 10px;

                &:hover, &.selected {
                    border-color: #2B78E4;
                }

                &:last-child {
                    margin-bottom: 0;
                }

                .top-line {
                    color: $TitleColor;
                    display: flex;
                    justify-content: space-between;

                    .action-buttons {
                        max-width: 85px;

                        .svg-icon {
                            height: 20px;
                            width: 20px;
                        }
                    }

                    .alignCenter div {
                        margin-right: 10px;
                    }

                    .md-button {
                        margin: 0;
                    }
                }

                .title {
                    font-weight: 500;

                    .mark {
                        color: orange;
                    }
                }

                .description {
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    width: 90%;
                }

                .status {
                    color: green;

                    &.DRAFT {
                        color: grey;
                        opacity: .8;
                    }
                }
            }
        }

        .kb-search {
            display: flex;
            flex-direction: row;
            margin: 5px 0;

            .kb-select {
                margin-right: 10px;
                width: 50%;

                .ember-power-select-selected-item {
                    display: block;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                }
            }

            .svg-icon-wrap {
                cursor: pointer;
                position: absolute;
                left: 12px;
                top: 50%;
                margin-top: -8px;
            }

            .md-input {
                padding-left: 35px;
                height: 100%;
            }
        }

        .reply-tab {
            color: #606a73;
            font-weight: 500;

            span {
                cursor: pointer;
                display: inline-block;
                margin-right: 14px;
                padding: 0 10px 3px;

                &.disabled {
                    pointer-events: none;
                    opacity: .3;
                }

                &.active {
                    color: #2B78E4;
                    pointer-events: none;
                    border-bottom: 2px solid;
                }
            }
        }

        md-input-container {
            padding: 0;
            display: block;
        }

        textarea {
            overflow: scroll;
            padding: 14px 127px 0 50px;
        }

        textarea, .txt-input {
            background: #fff;
            border: 1px solid #eeecec;
            border-radius: 5px 5px 0 0;
            min-height: 50px;
            cursor: auto;

            &.focus {
                border-color: #b1b1b1;
            }

            &.isReply {
                border-top: none;
                border-radius: 0;
            }
        }

        .action-text-editor {
            background-color: #f3f3f3;
            border-radius: 5px 5px 0 0;
            border: 1px solid #eeecec;
            border-bottom: none;
            padding: 5px;

            & + .txt-input {
                border-radius: 0;
            }

            &.focus {
                border-color: #b1b1b1;
            }

            .md-button {
                border-radius: 5px;
                cursor: pointer;
                height: 30px;
                margin: 0 5px;
                line-height: normal;
                min-width: auto;
                width: 30px;

                &.md-focused {
                    background-color: transparent;
                }

                &.active, &:hover {
                    background-color: #f0f0f0 !important;
                    color: #2b78e4 !important;
                }
            }
        }

        .wrap-input {
            position: relative;
            display: flex;
            flex-direction: column;

            &.reply {
                .txt-input, .actions-input {
                    background: #fff5f5;
                }
            }

            &.isLoader {
                opacity: .3;
            }

            &.internal {
                .txt-input {
                    padding-left: 10px;
                }
            }

            .wrap-reply {
                background-color: #fff;
                border: 1px solid #eeecec;
                border-bottom: none;
                border-radius: 5px 5px 0 0;

                &.focus {
                    border-color: #b1b1b1;
                }

                &.isEditor {
                    border-radius: 0;
                }

                .replyTo {
                    background-color: #eee;
                    margin: 5px;

                    &:after {
                        background-color: #999;
                    }
                }
            }

            .txt-input {
                padding: 5px 10px;
                outline: none;
                overflow: auto;
                max-height: 100px;
                border-bottom: none;

                > * {
                    display: inline-block;
                    vertical-align: middle;
                }

                .user-group {
                    background-color: #f2c4b2;
                    border-radius: 5px;
                    color: #989a9f;
                    pointer-events: none;
                    outline: none;
                    padding: 2px 4px;
                }
            }

            .txt-input[placeholder]:empty::before {
                content: attr(placeholder);
                color: #989a9f;
            }

            .txt-input[placeholder]:empty:focus::before {
                content: " ";
            }
        }

        .calendar-icon {
            display: none;
        }
    }

    .chat-calendar {
        visibility: hidden;
        display: none;
    }

    .wrap-user-check {
        max-height: 500px;
        overflow-y: auto;
        padding-top: 0;
    }
}

.tabs-message {
    width: 20%;
    overflow-x: hidden;

    *, ::after, ::before {
        box-sizing: border-box;
    }

    .wrap-search-block {
        padding: 11px 9px;
        margin-bottom: 4px;

        .search-block {
            align-items: center;
            display: flex;
            justify-content: space-between;
            position: relative;
        }

        .search-icon {
            height: 20px;
            position: absolute;
            width: 20px;
            z-index: 1;
            left: 7px;
        }

        .options-icon {
            cursor: pointer;
            height: 24px;
            width: 30px;
        }

        .search-input-chat {
            padding: 0;

            .md-input {
                padding: 0 5px 0 27px;
            }
        }

        .md-errors-spacer {
            height: 0;
        }
    }

    .search-result {
        color: #989a9f;
        padding: 10px;

        h4 {
            font-size: 13px;
            font-weight: 500;
            margin-bottom: 10px;
        }

        .search-result-item {
            background: #fafbfd;
            border-radius: 5px;
            border: 1px solid #e8f2ff;
            font-size: 11px;
            font-weight: 400;
            padding: 5px;
            margin-bottom: 10px;

            p {
                margin: 5px 0 0;
            }
        }
    }

    .bookmark-tab {
        align-items: center;
        cursor: pointer;
        display: flex;
        font-weight: 500;
        color: $TextGreyColor;
        padding: 5px 12px;
        margin-bottom: 4px;

        &.selected {
            background: $MainMenuBlueColor;
            color: #fff;

            .message-text {
                color: #ddd !important;
            }
        }

        .svg-icon {
            margin-right: 6px;
            height: 24px;
            width: 24px;
        }

        .bookmark-icon {
            fill: #d62516;
            margin-top: 6px;
        }
    }

    .message-icon {
        height: 20px;
        width: 20px;
    }

    .direct-icon {
        fill: green;
    }

    .thread-item {
        border-top: 1px solid #eeecec;
        border-left: none;
        padding: 5px 14px 7px;
        cursor: pointer;
        transition: border-width .2s;

        &.selected {
            background-color: #1e20a8;
            color: #fff;
            //background-color: #eee;
            //color: #121416;
            //border-left: 4px solid #1E20A8;
            //border-top: 1px solid rgba(101, 101, 101, .3);

            .top_txt .guest-name {
                color: #fff;
                font-weight: 700;
            }

            .message-text {
                color: #ddd !important;
                //color: #121416 !important;
            }
        }

        &:last-child {
            border-bottom: 1px solid #eeecec;
        }

        md-icon {
            &.active {
                color: #ffbb3e;
            }
        }
    }

    .item-user-chat {
        cursor: pointer;
        padding: 5px 0 5px 39px;
        display: flex;
        font-weight: 500;
        align-items: center;
        position: relative;

        &.selected {
            background: #1e20a8;
            color: #fff !important;

            .message-text {
                color: #ddd !important;
            }
        }

        &.unread {
            color: #2b78e4;
            font-weight: 700;
        }

        img {
            height: 28px;
            width: 28px;
        }

        .room-thread-action {
            align-items: center;
            display: flex;
            position: absolute;
            right: 5px;
        }

        .chat-entry-icon, .mentioned-icon {
            height: 20px;
            width: 20px;
            fill: #54a702;
        }

        .mentioned-icon {
            fill: #2b78e4;
            margin-left: 5px;
        }

        .lock-icon, .unlock-icon {
            height: 20px;
            margin-right: 5px;
            width: 20px;
        }

        .icon-status-user {
            background: #656a72;
            border-radius: 50%;
            border: 2px solid #fff;
            display: inline-block;
            height: 10px;
            margin: 18px 8px 0 -7px;
            width: 10px;
            z-index: 1;

            &.online {
                background: #54a702;
            }
        }
    }

    .cp-Panels {
        .arrow-icon {
            margin-right: 6px;
        }

        .internal-inner-panel {
            //padding: 0 10px 0 14px;
            padding: 0;
        }

        .menu-link {
            align-items: center;
            display: flex;

            .svg-icon {
                margin-right: 6px;
                height: 24px;
                width: 24px;
            }
        }

        .cp-Panel-toggle {
            padding: 0;
            align-items: center;

            > .alignCenter {
                padding: 10px 12px;
            }

            .md-button {
                height: 26px;
                margin: 4px 5px 0 0;
                padding: 0;
                width: 26px;
            }
        }

        .plus-circle-icon {
            height: 24px;
            width: 24px;
        }

        .top_txt {
            align-items: center;
            display: flex;
            justify-content: space-between;

            .guest-name {
                color: #121416;
                font-weight: 500;
            }

            .message-time {
                font-size: 11px;
            }
        }

        .property-text {
            font-size: 11px;
            font-weight: 500;
            margin-bottom: 7px;
        }

        .bottom_txt {
            justify-content: space-between;
            display: flex;
            font-size: 12px;

            .message-text {
                color: #5d5e64;
                font-size: 15px !important;
                font-weight: 500;
                flex: 1;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                margin-right: 5px;

                & * {
                    font-size: 15px !important;
                }
            }
        }
    }
}

.lock-icon {
    fill: #d62516;
}

.unlock-icon {
    fill: #54a702;
}

.visitor-info, .team-info, .user-info {
    //background-color: #f9fdff;
    width: 25%;
    padding: 5px;

    &.messages {
        padding: 0;
        display: flex;
        flex-direction: column;
        max-width: 25%;

        .divider {
            margin: 0 0 5px 0;
        }
    }

    &.column {
        display: flex;
        flex-direction: column;
        padding: 10px;

        .kb-name {
            align-items: center;
            justify-content: space-between;
            background-color: #eeecec;
            color: $TitleColor;
            display: flex;
            padding: 5px 10px;
            font-size: 20px;
            font-weight: 500;
            margin: -10px -10px 10px;

            .svg-icon {
                height: 22px;
                width: 22px;
            }

            .md-button {
                margin: 0 -10px 0 0;
            }
        }

        .title {
            margin-bottom: 10px;
            font-size: 18px;
            color: $TitleColor;
        }

        .author {
            align-items: center;
            display: flex;
            margin-bottom: 10px;
            font-size: 12px;

            .md-avatar {
                margin: 0 10px 0 0;
            }
        }
    }

    .send-link {
        margin: 10px 0;

        .primary-button {
            width: 100%;
        }
    }

    .ql-container {
        border: none;

        .ql-editor {
            padding: 0;
            white-space: normal;
        }
    }

    .action-buttons {
        align-items: center;
        display: flex;
        justify-content: space-between;

        .btn-link {
            height: 44px;
            margin-right: 10px;
            flex: 1;
            border: 2px solid #dadada;
            font-size: 12px;
            font-weight: 500;
            border-radius: 5px;
            line-height: 40px;
            letter-spacing: 0.7px;
            text-align: center;

            &:hover {
                border-color: #4a96ff;
                background-color: #fff;

                a {
                    color: #4a96ff;
                }
            }

            a {
                color: #adadad;
            }
        }
    }

    .user-avatar {
        text-align: center;
        margin-bottom: 10px;

        p {
            font-size: 18px;
            font-weight: 500;
            margin: 5px 0 14px;
        }
    }

    .user-name {
        color: #121416;
        font-weight: 500;
        margin-bottom: 5px;
    }

    .lock-icon, .unlock-icon {
        height: 20px;
        margin-right: 5px;
        width: 20px;
    }

    .add-member {
        display: flex;
        justify-content: flex-end;

        .md-button {
            margin: 0;
            padding: 0;
            width: auto;
        }

        span {
            color: #2b78e4;
            font-size: 12px;
            font-weight: 700;
        }
    }

    .plus-icon {
        height: 20px;
        margin-right: 2px;
        vertical-align: middle;
        width: 20px;
    }

    .created-date {
        font-size: 14px;
        margin: 0 0 15px 25px;
    }

    .title-name {
        font-weight: 500;
        color: #121416;
        display: flex;
        margin-bottom: 10px;

        .svg-icon {
            margin-right: 5px;
            height: 24px;
            width: 24px;
        }
    }

    .icon-status-user {
        vertical-align: text-top;
    }

    .delete-user {
        cursor: pointer;
        display: none;
        position: absolute;
        right: 0;
        top: 0;

        .svg-icon {
            height: 20px;
            width: 20px;
        }
    }

    .list-member {
        .item-member {
            margin-bottom: 7px;
            position: relative;

            &:hover {
                .delete-user {
                    display: block;
                }
            }

            .name {
                font-size: 15px;
                color: #121416;
            }

            .md-avatar {
                margin-right: 2px;
                vertical-align: middle;
            }
        }
    }

    .info-item {
        background-color: #fff;
        border: 2px solid #f5f5f5;
        border-radius: 3px;
        padding: 5px 10px;
        margin-bottom: 10px;

        .liquid-container, .liquid-child {
            overflow: visible;
        }

        .title-icon {
            margin-right: 7px;
            height: 20px;
            width: 20px;
        }

        .title-info {
            color: #121416;
            font-weight: 500;
            margin-bottom: 10px;
        }

        .profile-icon {
            fill: #989a9f !important;
            margin-right: 10px;
            height: 24px;
            width: 24px;
        }

        .cp-Panel-toggle {
            align-items: center;
        }

        .primary-button {
            width: 100%;
        }

        .description {
            margin-left: 25px;
        }

        .count-form {
            margin-right: 10px;
        }

        .progress-form {
            margin-bottom: 20px;
            position: relative;

            .bar {
                background: #54a702;
                border-radius: 2px;
                height: 8px;
                opacity: .3;
            }

            .line {
                background: #54a702;
                border-radius: 2px;
                position: absolute;
                left: 0;
                top: 0;
                height: 8px;
                width: 0;
            }
        }

        > .svg-icon {
            margin-right: 4px;
            vertical-align: middle;
        }

        .profile-item {
            display: flex;
            align-items: center;
            margin-bottom: 12px;

            .svg-icon {
                margin-right: 7px;
                height: 20px;
                width: 20px;
            }

            .alignCenter {
                flex-wrap: wrap;
            }
        }

        span {
            .svg-icon {
                vertical-align: bottom;
            }

            &.shopify {
                color: #5E8E3E;
            }

            &.bigcommerce {
                color: #32323B;
            }

            &.username {
                margin-left: 20px;
                flex: 1;
                color: $LinkColor;
            }
        }
    }

    .exchanges-section {
        min-height: 332px;

        .liquid-container {
            padding: 15px 0;
        }

        .img-item {
            width: 31%;
            margin-right: 3%;
            height: 100px;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;

            &:nth-child(3n + 0) {
                margin-right: 0;
            }
        }

        .doc-item {
            display: flex;
            margin-bottom: 10px;

            .doc-item-icon {
                margin-right: 5px;
            }

            .svg-icon {
                height: 20px;
                width: 20px;
            }
        }

        .links-tab {
            .link {
                display: inline-block;
                margin-bottom: 10px;
            }
        }
    }

    label {
        //font-size: 12px;
        margin-right: 5px;
        width: 100px;
    }

    .md-input {
        border-radius: 5px;
        background: #fff;
        padding: 0 5px;
    }

    .visitor-info-wrap {
        padding: 13px 15px;
    }

    .visitor-info-header {
        border-bottom: 2px solid #fbfbfb;
        color: #606a73;
        font-size: 18px;
        margin: 0;
        padding: 0 0 12px;

        .info {
            vertical-align: middle;
        }
    }

    .visitor-avatar {
        color: #121416;
        text-align: center;
        font-weight: 500;
        margin-bottom: 12px;

        .braincheck-avatar {
            width: 28px;
            margin: auto;
        }
    }

    .cp-Panel-body-inner {
        margin-top: 10px;
    }
}

.pinned-items {
    border: solid 2px #eff0f0;
    border-radius: 8px;
    display: flex;
    margin-bottom: 5px;
    padding: 10px;
    position: relative;

    .md-avatar {
        margin-right: 10px;
    }

    .content {
        overflow: hidden;
    }

    .unpin-button {
        margin: 0;
        padding: 0;
        position: absolute;
        right: 2px;
        top: 2px;
        width: 24px;
        height: 24px;

        .close-icon {
            height: 16px;
            vertical-align: middle;
            width: 16px;
        }
    }

